.sidebar__content {
  position: relative;
  height: 100%;
  flex-grow: 1;
  background: rgba(255, 255, 255, 0.02);

  @media (min-width: 425px) {
    width: calc(100% - 89px);
  }

  @media (max-width: 424px) {
    width: calc(100% - 83px);
  }

  @media (max-width: 374px) {
    width: calc(100% - 73px);
  }

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 1px;
    height: 1px;
    background-color: $white_font;
  }
}

.sidebar__content-inner,
.sidebar__content-inner_mod {
  box-sizing: border-box;
  height: 100%;
  margin-right: -7px;
  padding: 0 22px 0 15px;
  border-top: 1px solid $secondary_bg;
  border-left: 1px solid $secondary_bg;
  overflow-y: scroll;

  h2 {
    margin: 59px 0 57px 13px;
  }

  .content {
    .list-decor {
      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-gap: 15px;
      margin-top: 15px;
    }

    .list-decor__item {
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: $secondary_bg;

      @media (max-width: 767px) {
        height: 55px;
      }

      @media (max-width: 424px) {
        height: 50px;
      }

      @media (max-width: 374px) {
        height: 40px;
      }

      span,
      input {
        font-family: "Open Sans", sans-serif;
        font-weight: 300;
        text-align: center;
        color: $grey_font;

        @media (max-width: 767px) {
          font-size: 12px;
        }

        @media (max-width: 424px) {
          font-size: 11px;
        }

        @media (max-width: 374px) {
          font-size: 10px;
        }
      }
    }
  }
}

.sidebar__content-inner_mod {
  @media (min-width: 425px) {
    height: calc(100% - 85px);
  }

  @media (max-width: 424px) {
    height: calc(100% - 80px);
  }

  @media (max-width: 374px) {
    height: calc(100% - 70px);
  }
}

.sidebar__buttons-holder {
  display: flex;
  padding: 15px;
  border-left: 1px solid $secondary_bg;

  .shown-data-block {
    position: relative;
    flex-grow: 1;

    .shown-data-block__inner {
      display: flex;
      box-sizing: border-box;
      height: 100%;
      justify-content: center;
      align-items: center;
      border: 1px solid rgba(0, 0, 0, 0);
      background-color: $secondary_bg;
    }
  }
}
